/*Fonts*/

/*! Generated by Font Squirrel (https://www.fontsquirrel.com) */
@font-face {
    font-family: 'Vayentha Sans';
    src: url('fonts/vayenthasans-webfont.woff2') format('woff2'),
         url('fonts/vayenthasans-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Vayentha Script';
    src: url('fonts/vayenthascript-webfont.woff2') format('woff2'),
         url('fonts/vayenthascript-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Patrick Hand Regular';
    src: url('fonts/patrickhand-regular-webfont.woff2') format('woff2'),
         url('fonts/patrickhand-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}


html { 
  scroll-behavior: smooth; 
} 

/*Main App Page*/

.demo-big-content {
  background: #5D4157;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #A8CABA, #5D4157);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #A8CABA, #5D4157); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  position: fixed;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 100%;
}

.header-color a {
  font-family: "Patrick Hand Regular";
  font-size: 18px;
}

.header-color a:hover{
  background-color: rgba(255, 255, 255, 0.5);
  color: #5D4157;
  -webkit-transition: all 0.2s;
     -moz-transition: all 0.2s;
       -o-transition: all 0.2s;
          transition: all 0.2s;
}


/*Landing page*/

.landing-grid h1 {
  font-size: 240px;
  font-family: "Vayentha Script";
  line-height: 1.0;
  color: white;
  text-align: center;
  margin-top: -40px;
  margin-bottom: -10px;
  white-space: nowrap;
}

.banner-text {
  background-color: #30302D;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
  padding: 1.7em;
  text-align: center;
}

.banner-text h2 {
  font-size: 21px;
  font-family: "Patrick Hand Regular";
  color: white;
  text-align: center;
  padding-left: 2em;
  padding-right: 2em;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.links-to-pages {
  display: flex;
  justify-content: space-between;
  width: 75%;
  margin: auto;
}

.links-to-pages a {
  text-decoration: none;
}

.links-to-pages a:hover{
  background-color: rgba(255, 255, 255, 0.5);
  color: #ffffff;
  border-radius: 12px 12px 12px 12px;
  -webkit-border-radius: 12px 12px 12px 12px;
  -webkit-transition: all 0.5s;
     -moz-transition: all 0.5s;
       -o-transition: all 0.5s;
          transition: all 0.5s;
}

.links-to-pages i {
  color: white;
  font-size: 3.1em;
  padding-top: 10px;
  margin: auto;
  text-align: center;
}

.links-to-pages p {
  color: white;
  font-size: 21px;
  text-decoration-color: #30302D;
  font-family: "Patrick Hand Regular";
  margin-bottom: -10px;
  padding: 0.5em;
}


@media screen and (max-device-width: 550px) {
  .links-to-pages {
    width: 100%;
  }

  .banner-text {
    padding: 1.7em 1.0em;
  }

  .links-to-pages i {
    font-size: 2.1em;
  }

  .links-to-pages p {
    font-size: 18px;
  }
}

/*Sidebar*/

.drawer a {
  display: block;
  margin: 0 auto 5px auto;
  padding: 20px 0;
  text-align: center;
  font-size: 18px;
  font-family: "Patrick Hand Regular";
  font-weight: bold;
  border-left-width: 2px;
  border-left-style: solid;
}

.drawer a i.fa {
  display: block;
  font-size: 31px;
  margin-bottom: 5px;
}

.drawer a:hover{
  background-color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  color: #ffffff;
  -webkit-transition: all 0.5s;
     -moz-transition: all 0.5s;
       -o-transition: all 0.5s;
          transition: all 0.5s;
}


/*projects*/

.category-tabs {
  justify-content: center;
}

.category-tabs h4 {
  font-family: "Patrick Hand Regular";
  font-size: 18px;
  margin: 0.5px;
  line-height: 23px;
  color: #EDECEC;
}

@media screen and (max-device-width: 550px) {
  .category-tabs h4 {
    font-size: 15px;
    margin: 0.1px;
  }

  a.mdl-tabs__tab {
    padding: 0 8px 0 8px;
  } 
}

.category-tabs a:hover {
  text-decoration: none !important;
}

.projects-grid {
  display: flex;
  flex-wrap: wrap;
}

.projects-grid h2 {
  font-family: "Patrick Hand Regular";
  font-size: 24px;
}

.projects-grid p {
  font-family: "Patrick Hand Regular";
  font-size: 20px;
}

.projects-grid a {
  font-family: "Patrick Hand Regular";
  font-size: 17px;
  text-decoration: none;
}


/* Contact page */
.contact-body {
  margin: auto;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 75%;
  height: 500px;
  background: #30302D;
  opacity: .8;
  color: white;
  /*margin-bottom: 10px;*/
  border-radius: 10px;
}

.contact-grid h2 {
  font-family: "Vayentha Sans";
  font-size: 65px;
}

.contact-grid p {
  font-family: "Patrick Hand Regular";
  font-size: 18px;
  margin-top: 1em;
}

.contact-grid hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.contact-list a {
  text-decoration: none;
  color: white;
}

.contact-list i {
  padding-right: 1rem;
}

.contact-list li {
  color: white;
  font-size: 19px;
  font-family: 'Patrick Hand Regular';
}


/*Resume page*/

.resume-left-col {
  color: white;
  text-align: center;
}

.resume-left-col h3 {
  font-family: "Vayentha Sans";
  font-size: 45px;
}

.resume-left-col h4 {
  font-family: "Patrick Hand Regular";
  font-size: 16px;
  text-align: justify;
  margin-left: 17px;
  margin-right: 17px;
  line-height: 1.5;
}

.resume-summary {
  font-family: "Patrick Hand Regular";
  font-size: 17px;
  text-align: center;
  margin-left: 17px;
  margin-right: 17px;
  padding: 5px;
  border: 2px solid;
  border-radius: 10px;
  line-height: 1.5;
}

.resume-left-col img {
  height: 210px;
}

.resume-right-col {
  color: white;
  background: #30302D;
  opacity: .8;
  padding: 1.5em;
  font-family: "Patrick Hand Regular";
  font-size: 18px;
  border-radius: 10px;
}

.resume-right-col h2 {
  font-family: "Vayentha Sans";
  font-size: 65px;
}

.resume-right-col h4 {
  font-family: "Patrick Hand Regular";
  font-size: 24px;
}

.resume-right-col h6 {
  font-family: "Patrick Hand Regular";
  font-size: 15px;
  text-align: center;
}

.education-details {
  line-height: 1.2;
}

.education-details li {
  margin-top: 4px;
}

.experience-details {
  line-height: 1.2;
}

.skills-details {
  line-height: 1.0;
}

.resume-shortcuts a { 
  font-family: "Patrick Hand Regular";
  font-size: 16px;
  text-decoration: none; 
  color: white;
}

.resume-button {
  margin-left: 10px;
}

.resume-daterange {
  margin-top: 0px;
  font-family: "Patrick Hand Regular"; 
  font-size: 21px;
}

.resume-title {
  margin-top: 0px;
  font-size: 25px;
}

.resume-subtitle {
  margin-top: 0px;
  font-size: 19px;
}

.resume-details {
  margin-top: 12px;
  font-size: 19px;
}

.resume-section-break {
  border-top: 3px dotted;
}

.resume-item-break {
  border-top: 1px solid;
}


/* Stars */

.resume-left-col b {
  color: yellow;
}

.education-details b {
  color: yellow;
}

.experience-details b {
  color: yellow;
}
